      v-if="$route.name==='main' || ($route.name==='trash' && inside) || ($route.name==='hyp' && !inside)"
    >
        <select class="select" type="text" name="exchange" v-model="form.exchange" v-if="!inside">
          <option :value="item" v-for="item in $store.state.exchangeList" :key="item">{{item}}</option>
        </select>
        <toggle-button
                :labels="{checked: 'Alarm', unchecked: !this.$store.state.isAK?'Trade':'Alarm'}"
                :color="{checked: 'blue', unchecked: !this.$store.state.isAK?'rgba(255, 255, 255, 0.2)':'blue'}"
                @change="toggleAlarm"
                :value="radioAlarm"
                :width="60"
        />
<!--        <toggle-button-->
<!--                :labels="{checked: 'btc', unchecked: 'usd'}"-->
<!--                :color="{checked: 'orange', unchecked: 'green'}"-->
<!--                @change="toggleCurrency"-->
<!--                :value="radioCurrency"-->
<!--                :width="50"-->
<!--        />-->
  <!--      <toggle-button-->
  <!--              :labels="{checked: 'Alarm', unchecked: 'Alarm'}"-->
  <!--              :color="{checked: 'blue', unchecked: 'blue'}"-->
  <!--              @change="toggleAlarm"-->
  <!--              :value="radioAlarm"-->
  <!--              :width="60"-->
  <!--              v-if="$route.name==='ak'"-->
  <!--      />-->
        <toggle-button
                :labels="{checked: 'Sell', unchecked: 'Buy'}"
                :color="{checked: 'red', unchecked: 'limegreen'}"
                @change="toggleDirection"
                :value="radioDirection"
        />
        <input type="text" class="coinInput" name="coin" placeholder="coin" v-model='form.coin' v-if="!inside" required>
        <input type="text" class="amInput" name="amount" :placeholder="radioAlarm?'':'amount'" v-model.number='form.amount' :class="{'transparent':radioAlarm}">
        <input type="text" class="prInput" name="price" placeholder="price" v-model.number='form.price' @focus="setCurr(coin)" required>
        <div class="total" :class="{'transparent':radioAlarm}">({{total | numFormat('$ 0,0.00')}})</div>
        <input type="text" class="commentInput" name="comment" placeholder="comment" v-model='form.comment'>
        <button type="submit" class="add hoverNegative" v-if="$route.name==='main'">add</button>
        <input type="text" class="idInput" name="coin" placeholder="manual id" v-model='form.manualId' v-if="!inside">
        <button type="button" class="add hoverNegative" v-if="inside" @click="moveToTrashList">mark as loss (inv) and move to trashList</button>
        <br/>
        <div style='display: inline-block'>
          <img src="/img/loading9.gif" style='width:20px; margin-top:-10px' alt="loading" v-if="$store.state.isLoadingTrade"/>
        </div>
    </form>
    <div class="fc btnBlock" v-if="inside && $route.name!=='trash' && $route.name!=='hyp'">
<!--      <button type="button" class="holdBtn hoverNegative" :class="{'activeBtn':item?item.riskCategory === 'hold':false}" @click="markRiskCategory('hold')">hold</button>-->
<!--      <button type="button" class="safeBtn hoverNegative" :class="{'activeBtn':item?item.riskCategory === 'safe':false}" @click="markRiskCategory('safe')">safe</button>-->
<!--      <button type="button" class="riskyBtn hoverNegative" :class="{'activeBtn':item?item.riskCategory === 'risky':false}" @click="markRiskCategory('risky')">risky</button>-->
      <select class="safeBtn" type="text" name="risk" v-model="item.riskCategory" v-if="inside" @change="$store.dispatch('updateFB',{name:'tradeList',info:$store.state.isAK?'infoAK':'infoMain'})">
        <option value="hold">hold</option>
        <option value="safe">safe</option>
        <option value="risky">risky</option>
      </select>
      <select class="safeBtn" type="text" name="risk" v-model="item.industryCategory" v-if="inside" @change="$store.dispatch('updateFB',{name:'tradeList',info:$store.state.isAK?'infoAK':'infoMain'})">
        <option value="other">other</option>
        <option value="nft related coins">nft related coins</option>
        <option value="metaverse">metaverse</option>
        <option value="gaming">gaming</option>
        <option value="defi">defi</option>
        <option value="dex">dex</option>
      </select>
      <select class="safeBtn" type="text" name="ex" v-model="item.exchange" v-if="inside" @change="$store.dispatch('updateFB',{name:'tradeList',info:$store.state.isAK?'infoAK':'infoMain'})">
        <option v-for="itemm in $store.state.exchangeList" :value="itemm">{{itemm}}</option>
      </select>
    </div>
    <div class="mt10 p10 btnBlock card" v-if="(inside && $route.name!=='hyp')||(!inside && $route.name==='hyp')">
      <input type="number" class="totalInput" name="total" placeholder="total" v-model.number='form.total'><span class="label"> по {{item?item.currentPrice:''}} $/шт</span>
      <select class="hypBtn" type="text" name="hyp" v-model="hypTitle" v-if="inside">
        <option :value="item.title" v-for="item in $store.state[$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists" :key="item.id">{{item.title}}</option>
      </select>
      <button class="hypBtn hoverNegative mt10" @click="addHyp">add to Hyp</button>
    </div>
    <div class="mt20 btnBlock" v-if="$route.name==='main'">
    </div>
    <button type="button" @click="moveBackToTradeList" class="hoverNegative moveBack" v-if="$route.name==='trash' && inside">move back to tradeList</button>
  </section>
</template>

<script>

  import generateRandomId from '../helpers/generateRandomId';

  export default {
    props:['inside','coin','exchange','item'],
    components: {},
    data() {
      return {
        form: {
          exchange:this.exchange||'Binance',//для внутр формы
          coin: this.coin || '',//для внутр формы
          manualId: '',
          amount: null,
          price: null,
          comment: '',
          total:null,
        },
        radioDirection: false,
        radioAlarm: false,
        radioCurrency: false,
        hypTitle:this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists[this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists.length-1].title
      };
    },
    computed: {
      total() {
        if(this.$store.state.myGeckoList.length){
          const btcPrice = this.$store.state.myGeckoList.find(item=>item.symbol==='btc').current_price
          return (this.radioCurrency?this.form.price*btcPrice:this.form.price) * this.form.amount
        }
      },
      computedDirection(){
        if (this.radioDirection){
          return -1
        }else{
          return 1
        }
      },
    },
    methods: {
      add(){
        if(this.radioAlarm || this.$store.state.isAK){//аларм при рубильнике либо автоматом при АК
          const item = {//добав alarm
            inBtc:this.radioCurrency,
            id:generateRandomId(),
            coin:this.form.coin.toLowerCase(),
            date:new Date().toLocaleString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' }),
            desiredPrice: this.form.price,
            comment: this.form.comment,
            amount:this.computedDirection*(this.form.amount?this.form.amount:1),
          }
          this.$store.commit('addAlarm', { item,info:this.$store.state.isAK?'infoAK':'infoMain',listName:'alarmAltsList' });
        }else{
          const item = {//загрузить нов монету, и если она уже есть то добавить доп трейд в коммите
            inBtc:this.radioCurrency,
            exchange:this.form.exchange,
            price: this.form.price,
            comment: this.form.comment,
            id:generateRandomId(),
            manualId:this.form.manualId,
            coin:this.form.coin.toLowerCase(),
            amount:this.computedDirection*this.form.amount,
            date: new Date().toLocaleString('en-US'),
          }
          if(!this.form.amount){
            return alert('no amount typed, ignore')
          }
          this.$store.dispatch('loadAlt',item)
        }
        this.resetForm();
      },
      addHyp(){
        let amount
        if(this.form.total){
          amount = this.form.total/this.item.currentPrice
        } else amount = this.form.amount
        let price
        if(this.form.total){
          price = this.item.currentPrice
        } else price = this.form.price
        const hypListId = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].hypotheticalLists.find(item=>item.title===this.hypTitle).id
        let coinObj = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].tradeList.find(item=>item.coin===this.coin||this.form.coin)
        if(!coinObj){
          coinObj = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].trashList.find(item=>item.coin===this.coin)
        }
        // console.log(this.coin,coinObj)
        const coinUrl = coinObj.coinUrl
        const name = coinObj.name
        // console.log(coinUrl,name,exchangeUrl)
        const item = {
          currency:this.radioCurrency?'btc':'usd',
          exchange:this.form.exchange||'Binance',
          price,
          amount,
          comment: this.form.comment,
          id:generateRandomId(),
          manualId:this.form.manualId,
          coin:this.form.coin.toLowerCase(),
          coinUrl,
          name,
          date: new Date().toLocaleString('en-US'),
          hypListId,
        }
        if(!this.form.total&&!this.form.price&&!this.form.amount){
          return alert('no price and amount typed, ignore')
        }
        this.$store.dispatch('loadAlt',item)
        // this.$store.commit('addItemToHyp', { item })
        this.resetForm();
      },
      moveToTrashList(){
        console.log('moving to trashlist...')
        this.$store.commit('moveToTrashList',{coin:this.coin})
      },
      moveBackToTradeList(){
        console.log('moving back to tradelist...')
        this.$store.commit('moveBackToTradeList',{coin:this.coin})
      },
      changeExchange(coin){
        const coinObj = this.$store.state.infoMain.tradeList.find(item=>item.coin===coin)
        const ex = prompt('заменить биржу на', '')
        if (ex) {
          coinObj.exchange = ex
          coinObj.exchangeUrl = '/img/'+ex+'.jpg'
          this.$store.dispatch('updateFB',{name:'potentialList',info:this.$store.state.isAK?'infoAK':'infoMain'});

        }
      },
      toggleDirection({ value }) {
        this.radioDirection=value;
      },
      toggleAlarm({ value }) {
        this.radioAlarm=value;
      },
      toggleCurrency({ value }) {
        this.radioCurrency=value;
      },
      setCurr(coin) {
        this.form.price = this.$store.state.myGeckoList.find(item=>item.symbol===coin).current_price;
      },
      resetForm() {
        this.form.exchange = this.exchange || 'Binance';
        this.form.coin = this.coin || '';
        this.form.amount = '';
        this.form.price = '';
        this.form.comment = '';
        this.form.manualId = '';
      },
    },
  }
</script>

